.animatedBackground {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background: transparent; /* Transparent background */
    overflow: hidden;
}

.animatedBackground span {
    width: 1vmin;
    height: 1vmin;
    border-radius: 1vmin;
    backface-visibility: hidden;
    position: absolute;
    animation: move;
    animation-duration: 45s; /* Duration units added */
    animation-timing-function: linear;
